.root {
  width: 100%;
  min-height: 100vh;
}


/* header */
.header {
  width: 100%;
  height: 125px;
  padding: 15px;
  background: #49368A;
  color: #fff;
  display: flex;
}

/* main */
.main {
  width: 100%;
  height: calc(100vh - 130px);
  display: flex;
  margin-top: 5px;
  border-top: 5px solid #49368A;
}

/* Sidebar */
.sidebar {
  width: 250px;
  padding-top: 15px;
  background: linear-gradient(0deg, #fff, #D8C5FF);
  border-left: 3px solid #49368A;
}

/* body */
.body {
  width: auto;
  padding: 15px;
}


.err-root, .err-root h3 {
  display: none !important;
  opacity: 0;
}

